тором, оснащенные взрывобезопасным корпусом. Уровень и вид взрывозащиты – PB Ex d I Mb X обеспечивает безопасную эксплуатацию электродвигателей в рудниках и шахтах, а также в помещениях, где могут скапливаться взрывоопасные газы. Степень защиты корпуса двигателя от внешних воздействий – IP54/ IP55/ IP67.")]),_c('p',[_vm._v("Электродвигатели АВРМ предназначены для продолжительной работы от сети переменного тока с напряжением 1140/660/380 Вольт и частотой тока 50 Гц. Класс энергоэффективности агрегатов – IE1 / IE2, климатическое исполнение – У2 / У5. Номинальный режим функционирования электродвигателя – S1, частота вращения – 1500 об/мин. или 3000 об/мин.")]),_c('p',{staticClass:"second"},[_vm._v("Оснащение двигателей АВРМ «Элдвиг СПб» и дополнительные опции:")]),_c('ul',[_c('li',[_vm._v("вариативное исполнение конца вала;")]),_c('li',[_vm._v("вариативное оснащение узлов подшипников;")]),_c('li',[_vm._v("импортозамещение зарубежных аналогов;")]),_c('li',[_vm._v("фабричная окраска в колер заказчика.")])]),_c('p',[_vm._v("Средний срок эксплуатации электродвигателей серии АВРМ производства «Элдвиг СПб» составляет 15 лет. Это надежное и эффективное оборудование, удовлетворяющее всем современным требованиям.")]),_c('h3',{staticClass:"first"},[_vm._v("Взрывобезопасные электродвигатели АВРМ: достоинства и сферы использования")]),_c('p',[_vm._v("Двигатели серии АВРМ используются в приводах вентиляторов местного проветривания. Взрывозащищенный корпус позволяет использовать агрегаты в рудниках, угольных и соляных шахтах, где имеется опасность по газу метану и/или угольной пыли, в том числе и на тупиковых выработках.")]),_c('p',{staticClass:"second"},[_vm._v("Достоинства электродвигателей АВРМ «Элдвиг СПб»:")]),_c('ul',[_c('li',[_vm._v("применение только высококачественных намоточных, изоляционных и пропиточных материалов гарантирует безотказную работу агрегатов в течение 30 тыс. часов в режиме S1;")]),_c('li',[_vm._v("использование динамной электротехнической стали М530-А50, из